.preLogo {
    opacity: .3;
    height: 250px;
    animation-name: stretch;
    animation-duration: 1.5s; 
     animation-timing-function: ease-out; 
     animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    animation-fill-mode: none;
     animation-play-state: runnin
}

@keyframes stretch {
    0% {
      transform: scale(.6);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1.7);
    }
  }